import React, { Suspense, useCallback, useMemo, useRef } from 'react';
import tachyons from 'tachyons';
import "./OurProduct.css";
import soldier from "./soldier.png";
class OurProduct extends React.Component {

 render()
  {
    
    return (
        <div className='OurProduct'>
            <hr className="hrbreak ma0 w4 center"></hr>
            <p className="white-70 b tc fontfix">Our Product</p>
            <hr className="hrbreak ma0 w4 center"></hr>
            <div className="OurProductContent">
                <img className="soldier" src={soldier} alt=''></img>
                <div className="center" >
                    <p className="white b f3">Check Out our New Python game!!!!</p>
                    <div class="content">
                        <div class="slider-wrapper">
                            Make with
                            <div class="slider">
                                <div class="slider-text1">Python</div>
                                <div class="slider-text2">Pygame</div>
                                <div class="slider-text3">VSCode</div>
                            </div>
                        </div>       
                    </div>
                    <button className="GameBtn b "> Learn more!</button>
                </div>
            </div>
            

        </div>
    );
   }
}

export default OurProduct;
